.tweetBox {
  padding-bottom: 10px;
  border-bottom: 8px solid var(--twitter--background);
  padding-right: 10px;
}

.tweetBox > form {
  display: flex;
  flex-direction: column;
}

.tweetBox--input {
  padding: 20px;
  display: flex;
}

.tweetBox--input > input {
  flex: 1;
  font-size: 20px;
  margin-left: 20px;
  border: none;
}

.tweetBox--imageInput {
  border: none;
  padding: 40px;
}

.tweetBox--tweetBtn {
  background: var(--twitter--color) !important;
  color: #fff !important;
  font-weight: bold !important;
  width: 130px !important;
  height: 40px !important;
  border-radius: 30px !important;
  margin-top: 20px !important;
  margin-left: auto !important;
}